
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    itemName: {
      type: String,
      required: true
    },
    itemCost: {
      type: String,
      required: true
    }
  },
  setup(props, { emit }) {

    function addNewItem() {
      emit('add-new-item')
    }
    return { addNewItem }
  }
})
