
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'NewContractItem',
  props: {
    itemSelectedProp: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    }
  },
  setup(_, { emit }) {
    const itemSelected = ref('')

    const addItem = (): void => {
      emit('add-item')
    }

    return {
      itemSelected,
      addItem
    }
  }
})
